import React from 'react';
import {motion} from "framer-motion";
import Second_carousel from '../Second_carousel';
import '../Styles.css';
import HomeCarousel from '../components/Carousel';
import { Link } from 'react-router-dom';


function Home() {

  return (
      <section className="home-container">
      <h2>Exclusive Collections</h2>
        <HomeCarousel /><br />
      <h2>Exclusive Outlook</h2>
      <motion.div className='second-carousel'>
        <motion.div drag="x" dragConstraints={{right: 0, left: -1200}} className='inner-slide-carousel'>
          {Second_carousel.map(image => {
            return(
              <motion.div className='slide-items'>
              <Link to="./Product" ><img src={image} className='slide-images' alt="Slide Image" /></Link>
              </motion.div>
            );
          })}
        </motion.div>
      </motion.div>
      </section>
  )
}

export default Home;