import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faEnvelopesBulk } from '@fortawesome/free-solid-svg-icons';
import { Button } from 'react-bootstrap';



function Contact() {

  function Submitted(){
    alert("Thank you for your Valuable FeedBack !")
  }

  return (
    <div className='contact-body'>
      <h1>Let us know your convience</h1>
      <div className='contact-container'>
        <div className='contact-left-container'>
        <FontAwesomeIcon className='envelope-svg' icon={faEnvelopesBulk} />
        </div>
        <div className='contact-right-container'>
          <h3>Name</h3>
          <input type='text' placeholder='John...'/>
          <h3>Mail Address</h3>
          <input type='mail' placeholder='..@gmail.com'></input>
          <h3>Your Suggestion</h3>
          <textarea placeholder='Your comments here...' rows={5} cols={50}></textarea>
          <br/>
          <Button onClick={Submitted} className='contact-btn'>Submit</Button>
        </div>
      </div>
    </div>
  )
}

export default Contact;