import React from 'react'
import Carousel from 'react-multi-carousel'
import Card from './Card'
import { menswear, womensWear, accessories } from '../asserts/Products/ProductImages'
import 'react-multi-carousel/lib/styles.css'
import '../Styles.css'

function ProductCard() {

  function createMensWearCard(mensCard){
    return <Card
    imgSrc={mensCard.image}
    title={mensCard.detail}
    price={mensCard.price} 
    />
  }

  function createWomensWearCard(womensCard){
    return <Card 
      imgSrc={womensCard.image}
      title={womensCard.detail}
      price={womensCard.price}
    />
  }

  function createAccessoriesCard(accessoriesCard){
    return <Card
    imgSrc={accessoriesCard.image}
    title={accessoriesCard.detail}
    price={accessoriesCard.price}
     />
  }


    const responsive = {
      superLargeDesktop: {
        breakpoint: { max: 4000, min: 3000 },
        items: 5
      },
      desktop: {
        breakpoint: { max: 3000, min: 1024 },
        items: 4
      },
      tablet: {
        breakpoint: { max: 1024, min: 464 },
        items: 2
      },

      mobile: {
        breakpoint: { max: 464, min: 0 },
        items: 2
      }
    };
    return (
        <div className="product-heading">
        <h2>For Men</h2>
        <Carousel responsive={responsive} infinite={true} className='mens-product-container'>
      {menswear.map(createMensWearCard)}
      {/* <Card imgSrc="" title="White Shirt Long Sleeve Casual" price="rs. 2499" /> */}
    </Carousel><br />
    <h2>For Women</h2>
    <Carousel responsive={responsive} infinite={true} className='womens-product-container'>
      {womensWear.map(createWomensWearCard)}
    </Carousel><br />
    <h2>Accessories</h2>
    <Carousel responsive={responsive} infinite={true} className='accessories-product-container'>
      {accessories.map(createAccessoriesCard)}
    </Carousel>
        </div>
      );
    }

    export default ProductCard;