import React from 'react'
import Slider from 'react-slick'
import '../asserts/First_carousel/Banner1.png'
import Banner1 from '../asserts/First_carousel/Banner1.png'
import Banner2 from '../asserts/First_carousel/Banner2.png'
import Banner3 from '../asserts/First_carousel/Banner3.png'
import 'slick-carousel/slick/slick.css'
import 'slick-carousel/slick/slick-theme.css'

function HomeCarousel(){

    const settings = {
    dots: false,
    infinite: true,
    speed: 3000,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 2000,
    }

    return <div>
        <Slider {...settings} className="banner-container">
            <div>
                <img src={Banner1} alt="Banner1"/>
            </div>
            <div>
                <img src={Banner2} alt="Banner2" />
            </div>
            <div>
                <img src={Banner3} alt="Banner3" />
            </div>
        </Slider>
    </div>
}

export default HomeCarousel;